.nav-link a{
  text-decoration: none;
  text-underline-offset: none;
}
#nav-menu{
  background-color: teal;
  height: 70px;
  justify-content: center;
  align-items:center;
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}
.typewritter{
  color: white;
}

.links{
margin-left: 00px;

justify-content: center;

display: flex;
gap: 30px;
align-items:center
}
html{
  scroll-behavior: smooth;
}


.links h3{
  font-weight: 300;
}
.links button{
  --width: 110px;
    --height: calc(var(--width)/3);
    background: linear-gradient(90deg,#379689,#05386b);
    border: 1px solid #b8860b;
    border-radius: 2em;
    box-shadow: 2px 0 0 5px rgb(0 0 0 / 20%);
    color: #edf5e1;
    font-family: sans-serif;
    font-size: calc(var(--height)/2.5);
    height: var(--height);
    letter-spacing: .2em;
    line-height: var(--height);
    margin-left: 700px;
    overflow: hidden;
    position: relative;
    text-align: center;
    text-shadow: 6px 3px 2px rgb(0 0 0 / 20%);
    -webkit-transform: perspective(500px) rotateY(-15deg);
    transform: perspective(500px) rotateY(-15deg);
    transition: .5s;
    width: var(--width);
}
.links button:hover{
  color: brown;
  background-color: white;
}

